<template>
  <div class="bg-white">
    <section class="bg-white text-white pt-6">
         <!-- <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->

         <article class="bg-blue pb-32">
            <h2 class="text-3xl py-6 text-white
            md:text-6xl
            lg:text-4xl">- Contactenos -</h2>
            <!-- <span class="font-Clarissa text-5xl">Contactenos</span> -->            

            <section class="container border-dashed border-2 rounded border-white p-5 m-auto">
              <form class="flex flex-1 justify-between gap-x-3 gap-y-6 flex-wrap shadow-round bg-darkBlue text-xl px-10 py-8"
              @submit="formSubmit">
                <input name="name" 
                type="text" 
                placeholder="Full Name" 
                v-model="name" 
                min="0" 
                class="w-full px-6 py-2 text-left border md:w-1/2 rounded-4xl text-orange bg-darkBlue">

                <input name="phone" 
                type="text" 
                placeholder="Phone" 
                v-model="phone" 
                min="0" 
                class="w-full px-6 py-2 text-left border md:w-1/5 rounded-4xl text-orange bg-darkBlue">

                <input name="email" 
                type="email" 
                placeholder="Email" 
                v-model="email" 
                min="0" 
                class="w-full px-6 py-2 text-left border md:w-1/4 rounded-4xl text-orange bg-darkBlue">

                <textarea name="comentarios" id="" 
                placeholder="Comments"
                class="w-full h-40 px-8 py-6 text-left border rounded-4xl text-orange bg-darkBlue"
                ></textarea>

                <div class="w-full flex gap-3">  
                  <span class="inline cursor-pointer border rounded-4xl bg-darkBlue px-2 py-1 w-10 h-10 md:w-auto md:h-auto"
                  @click="activeTerm = !activeTerm"
                  :class="isActiveTerm">
                    <i class="fas fa-check"></i>
                  </span>
                  <label class="text-orange self-center"
                  @click="activeTerm = !activeTerm"
                  >Términos y condiciones</label>
                </div>

                <div class="flex flex-col w-full gap-3 md:flex-row">
                  <span class="p-2 px-4 text-left bg-red rounded-xl md:w-3/5" v-show="error">{{ error_message }}</span>
                  <span class="p-2 px-4 text-left bg-green-500 rounded-xl md:w-3/5" v-show="success">La solicitud fue enviada exitosamente!!!</span>
                  <button type="button" class="px-12 py-1 text-base text-white uppercase md:text-xl bg-orange rounded-4xl md:ml-auto"
                  @click="formSubmit">Enviar Solicitud</button>
                </div>                
              </form>
            </section>
          </article>
         
    </section>
    
  </div>
</template>
<script>
import Authorization from "@/services/user/auth.service";

export default {
  name: "ContactComp",
  data() {
    return {
      success: false,   
      error_message: 'Error. No se pudo enviar la solicitud!!!',   
      error: false,               
      activeTerm: false,
      name: '',
      phone: '',
      email: '',      
    }
  },
  computed: {
    isActiveTerm() {
      return (this.activeTerm) ? 'text-orange' : 'text-darkBlue'
    }
  },
  methods: {
    async formSubmit() {
      const body = {
        subject: this.subject,
        mail: this.email,
        message: {
          name: this.name,
          phone: this.phone,
          email: this.email,
          message: this.message,
          msg_info: this.msg_info
        },
      }  

      if(this.validEmail(this.email)) {        
        if(await Authorization.checkLogin('/contact-us', {body, auth: 'bearer'}, 'post')) {
          localStorage.removeItem('request_info')
          this.success = true 
          setTimeout(() => {
              this.success = false
          }, 3000); 
        } else {
          this.error = true 
          setTimeout(() => {
              this.error = false
          }, 3000);          
        }
      } else {
        this.error = true 
        this.error_message = 'Debe insertar una dirección de correo válida.'
          setTimeout(() => {
              this.error_message = 'Error. No se pudo enviar la solicitud!!!'
              this.error = false
          }, 3000);          
      }
    },    
    validEmail(mail) {
      if(mail) {
        if(mail.match(/^[\w-.]+@([\w-]+\.)+[\w-]{2,8}$/))
          return true
      }
      return false
    },
  }
};
</script>
